import React, { Component } from "react";
import { connect } from "react-redux";
// import { isMobile } from 'react-device-detect';
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import { AutoScroll } from '@splidejs/splide-extension-auto-scroll';
import { Grid } from '@splidejs/splide-extension-grid';
import Card from "./Elements/Card";
import { getDataNextSteps } from '../../../../actions/themeActions'
import '../../scss/ThemesSplide.scss'
import './scss/Motion.scss'
import { isEmbed, isSocialEmbed, isSocialWall, isTagEmbed, isTagShop } from "../../../../actions/api"
class Motion extends Component {
  constructor(props) {
    super(props);
    // this.inputRefs = React.createRef();
    this.state = { parent_id: '', postData: [] }
    this.inputRefs = {};
  }
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
    const { postData, heightUpdate } = this.props
    this.setState({ postData })
    setTimeout(() => heightUpdate(), 1000)
  }

  requestData = () => {
    const { appendData, wall, preRender, hasMoreData, getDataNextSteps } = this.props;
    if (hasMoreData) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  autoScrollSlider = (splide, prev, next, destIndex) => {
    const { wall } = this.props
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    if (autoScrollStatus == 1 || isTagShop) {
      const currentIndex = isTagEmbed ? (splide["_o"].perPage + splide.index) : splide.index
      const slideLength = splide.length
      const permove = isTagEmbed ? 1 : splide["_o"].perPage
      if (slideLength - currentIndex === permove) this.requestData()
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.postData !== this.props.postData) return true
    else return false
  }

  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, onClickToCTA, postSizeInPx, parentID, keyRender, loaderData } = this.props;
    const inputName = parentID;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    const rowCount = !wall.ThemeRule.multiRow || wall.ThemeRule.multiRow === 0 ? 2 : wall.ThemeRule.multiRow;
    var newPostdata = postData
    let isVisible = true

    newPostdata = newPostdata.length < 12 ? Array(15 - newPostdata.length).fill(newPostdata).flat() : newPostdata;
    const isRowsData = wall.ThemeRule.multiRow == 2 ? [1, 2] : [1]
    const cloneStatus = isSocialEmbed ? {} : isRowsData?.length > 1 ? {} : { clones: 0 }
    const halfIndex = Math.ceil(newPostdata.length / 2)
    const upperPosts = isRowsData.length > 1 ? newPostdata.slice(0, halfIndex) : newPostdata
    const lowerPosts = newPostdata.slice(halfIndex);


    const isType = autoScrollStatus != 1 ? Object.keys(cloneStatus).length > 0 ? false : (isTagShop ? 'slide' : 'loop') : (isTagShop ? 'slide' : 'loop')
    const settings1 = {
      start: 0,
      type: `loop`,
      rewind: false,
      speed: 1000,
      interval: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : isTagEmbed ? 3 : 6,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      pagination: false,
      pauseOnHover: false,
      arrows: false,
      autoScroll: {
        speed: wall.ThemeRule.multiRow == 2 ? -1 : 1,
        pauseOnHover: false,
      },
      breakpoints: {
        560: {
          perPage: mobileColumn > 0 ? mobileColumn : 1,
        },
        767: {
          perPage: mobileColumn > 0 ? mobileColumn : 2,
        },
        991: {
          perPage: mobileColumn > 0 ? mobileColumn : 3,
        },
        1200: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 4,
        },
        1400: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 5,
        },
        1600: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : isTagEmbed ? 3 : 6,
        }
      },
    };

    const settings2 = {
      ...cloneStatus,
      type: autoScrollStatus === 1 ? false : isType,
      rewind: true,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : isTagEmbed ? 3 : 6,
      perMove: 1,
      gap: 0,
      padding: 0,
      pagination: false,
      arrows: false,
      pauseOnHover: false,
      autoScroll: {
        speed: 1,
        pauseOnHover: false,
      },
      breakpoints: {
        560: {
          perPage: mobileColumn > 0 ? mobileColumn : 1,
        },
        767: {
          perPage: mobileColumn > 0 ? mobileColumn : 2,
        },
        991: {
          perPage: mobileColumn > 0 ? mobileColumn : 3,
        },
        1200: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 4,
        },
        1400: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 5,
        },
        1600: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : isTagEmbed ? 3 : 6,
        }
      },
    };

    return (<>
      <div className="tb_c_m_post_container">
        {isRowsData.map((row, index) => {
          const isRowTwo = row == 2;
          const posts = isRowTwo ? lowerPosts : upperPosts;
          const settings = isRowTwo ? settings2 : settings1;

          return (
            <Splide
              key={index}
              hasTrack={false}
              id={`slider_${row}`}
              className={`tb_c_m_post_slider slider_${row}`}
              options={settings}
              extensions={{ AutoScroll }}
              ref={(parentID) => (this.inputRefs[inputName] = parentID)}
              onMove={this.autoScrollSlider}
            >
              <SplideTrack>
                {posts?.length > 0 && posts.map((item, index) => {
                  const cardData = completeDataObject[item];

                  return (
                    <SplideSlide key={`tb_${index}_${index}`} className={`tb_${index}`}>
                      <div className="tb_c_m_col_wrap">
                        <Card
                          key={item}
                          parentID={parentID}
                          ownerId={wall.Wall.owner}
                          wall={wall}
                          itemData={cardData}
                          itemIndex={item}
                          personalization={wall.Personalization}
                          ThemeRule={wall.ThemeRule}
                          clickToShowPopUp={clickToShowPopUp}
                          wallID={wall.Wall.id}
                          onClickToCTA={onClickToCTA}
                          postSizeInPx={postSizeInPx}
                        />
                      </div>
                    </SplideSlide>
                  );
                })}
              </SplideTrack>
            </Splide>
          );
        })}
      </div></>)
  }
}

const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Motion);