import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../../Elements/PostContent/PostContent'
import { PostTrimContent } from '../../../../../utils'
import Network from "../../../Elements/Network/Network";
import ShopButton from "../../../Elements/ShopButton/ShopButton";
import { isSocialWall, isTagEmbed, isTagShop, isTagbox, isEmbed } from "../../../../../actions/api";
import ShareOnHover from "../../../../Themes/Elements/ShareOnHover/ShareOnHover"

const trim_content_length = 50;
const Card = ({ itemData, personalization, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, postSizeInPx, wall, isPadding, isRowCount }) => {
  const isProduct = !isSocialWall && itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
  const isShopIcon = (isProduct && itemData.hotspot === 0) ? true : false
  const isTagshopHover = ThemeRule.postHover === "" || ThemeRule.postHover !== 1
  const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5) ? true : false;
  const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
  const responsiveCardSize2x = postSizeInPx.adjustWidth < 250 ? true : false
  const isHotspot = itemData.hotspot && isProduct ? true : false
  const isShopBtn = isTagShop && (!itemData.hotspot && isProduct) ? true : false;
  const ctaStatus = !ThemeRule.hideContent;
  const isContent = isShopIcon ? ctaStatus == 0 ? true : false : true
  const shopBtn = isShopIcon ? ctaStatus == 0 ? false : true : false
  const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
  const overlay = isTagbox ? 'tb_c_m_color_overlay' : 'tb_c_m_post_overlay-' + themeMode
  const overlayStyle = { backgroundColor: isTagbox ? ThemeRule.backgroundColor : '' }
  const outerpadding = isSocialWall ? { padding: ThemeRule.spacing } : null

  return (
    <div id={`tb-hs-post-${itemData.id}`} className={`tb_c_m_post_wrapper${responsiveCardSize2x ? ' tb_c_m_post_wrapper2x' : ''}`} tb-network={itemData.network.id} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0} style={{ padding: personalization.padding / 2 }}>

      <div className={`tb_c_m_post_in${isTagShop ? ' tb_c_m_post_ani' : ''}`} onClick={clickToShowPopUp(itemIndex, itemData)} style={outerpadding} >
        <div className="tb_c_m_post_media_wrapp">
          {mediaType ? <Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} /> : ''}
          {isEmbed ? <>
            <div className="tb_c_m_post_details">
              <div className="tb_c_m_contant_wrapper">
                {isTagShop ? <div className="tb_c_m_social_">
                  <Network networkClass={'tb_c_m_social__ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} Personalization={personalization} />
                </div> : null}
                {shopBtn ? <ShopButton ThemeRule={ThemeRule} btnClass={'tb_c_m_btn_wrap'} /> : null}
                {(isContent || itemData.type == 1) ? <PostContent contentClass={'tb_c_m_content'} item={itemData} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} isNetwork={true} /> : null}
              </div>
              {!isTagShop ?
                <AuthorDetails key={`img${itemIndex}_${itemData.id}`} ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
                : null}
              {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={`tb_c_m_share_container ${isTagEmbed ? 'tb_c_m_share_container_te' : ''}`} item={itemData} wallID={wallID} ownerId={ownerId} /> : null}
            </div>
          </> : null}
        </div>
      </div>
    </div>
  );
}
export default Card