import React, { Suspense } from "react";
import { isTagEmbed, isTagShop, isSocialEmbed } from "../../../../../actions/api";
import MediaIcon from "../../../Elements/MediaIcon/MediaIcon";
import Image from "../../../Elements/Media/Image";
import Video from "../../../Elements/Media/Video";
const Like = React.lazy(() => import('../../../Elements/Reactions/Like'));

const Media = ({ itemData, wallID, ThemeID, ThemeRule, ownerId, wall }) => {

  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  const mediaIcon = isTagShop && itemData.hotspot ? false : true
  return <div className="tb_c_m_media_wrap">
    {isSocialEmbed ? <Suspense fallback={<></>}><Like likeClass="tb_c_m_like" postId={itemData.id} wallID={wallID} UserDetail={wall.UserDetail} /></Suspense> : null}
    {isSocialEmbed && wall.Personalization.interactive ? <MediaIcon itemData={itemData} IconClass="tb_c_m_media_icon" show={isSocialEmbed} /> : null}
    {autoPlay ?
      <Video VideoClass="tb_c_m_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} isCover={true} muted={true} autoPlay={autoPlay} handleVideoEnded={() => { }} />
      :
      <Image ImageClass="tb_c_m_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} ThemeRule={ThemeRule} hotspot={true} />
    }
  </div>
}
export default Media