import Author from "../../../Elements/Author/Author";
import Time from "../../../Elements/Time/Time";
import Network from "../../../Elements/Network/Network";
import { isTagEmbed } from "../../../../../actions/api";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font }) => {
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;

  return (
    <div className={`tb_c_m_author_wrapper ${isTagEmbed ? 'tb_c_m_author_wrapper_top' : ''}`}>
      <div className="tb_c_m_author">

        {postAuthor ? <Author network={network} author={author} authorClass={'tb_c_m_author_profile'} /> : ''}
        <div className="tb_c_m_author_info">
          {postAuthor ? <div className="tb_c_m_authorname">{author.name}</div> : ''}
          <div className="tb_c_m_post_info">
            {postAuthor ? <div className="tb_c_m_username">{`@${author.username}`}</div> : ''}
            {authorSeprator ? <div className="tb_c_m_seprator"> </div> : ''}
            {personalization.postTime ? <Time postTime={postTime} timeClass={`tb_c_m_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /> : ''}
          </div>
        </div>
      </div>
      {!isTagEmbed ?
        <div className="tb_c_m_social_">
          <Network networkClass={'tb_c_m_social__ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} />
        </div>
        : null
      }
    </div>
  );
}
export default AuthorDetails